import React from "react";
import "./styles.css";

export default class App extends React.Component {
  constructor() {
    super();
    this.state = {
      counter: 0
      // buttonStatus : 'play',
    };
    var interval;
    var buttonStatus = "play";
  }
  HandlingCounters = () => {
    let { counter } = this.state;
    if (this.buttonStatus === "play") {
      this.buttonStatus = "pause";
      this.interval = setInterval(() => {
        counter += 1;
        this.setState({ counter });
      }, 1000);
    } else {
      clearInterval(this.interval);
      this.buttonStatus = "play";
      //this.setState({buttonStatus});
    }
  };

  render() {
    let a;
    if (this.state.counter <= 10) {
      a = (
        <div style={{ fontSize: 20, margin: "20px" }}>
          Counter:{this.state.counter}
        </div>
      );
    } else {
      a = <div style={{ fontSize: 20, margin: "20px" }}>Counter:{10}</div>;
    }
    return (
      <div className="App">
        {a}
        <div style={{ fontSize: 20, margin: "20px" }}>
          Counter1:{this.state.counter}
        </div>
        <button
          style={{ padding: "5px 60px", marginBottom: 1 }}
          onClick={this.HandlingCounters}
        >
          play
        </button>
      </div>
    );
  }
}
